.link_home {
    display: block;
    background-color: var(--bg-color);
    color: var(--accent-bg-color);
    padding: 1rem;
    text-align: center;
    text-decoration: none;
    width: 100%;
    border: 1px solid var(--accent-bg-color);
    border-radius: 0.5rem;
    cursor: pointer;
}

.link_home:hover {
    background-color: var(--accent-bg-color);
    color: var(--accent-fg-color);
}