.settings_theme_container {
    display: flex;
    flex-direction: row;
    gap: 0.5rem;
}

.settings_theme_button {
    display: block;
    padding: 0.75em;
    width: 100%;
    text-align: center;
    text-decoration: none;
    border: 1px solid var(--fg-color);
    border-radius: 0.5rem;
    cursor: pointer;
}